.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-home--container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo-image{
  width: 70px;
  height: 50px;
  border-radius: 5%;
  overflow: hidden;
  align-items: center;
}

.navbar-custom {
  color: #FFFFFF;
  background-color: #2e2f30;
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.auth--container{
	max-width: 500px;
	width: 95%;
	margin: 0 auto;
}
.auth--container{
	text-align: center;
}
.auth--container label{
	display: block;
	text-align: left;
}
.auth--container input{
	border: none;
	border-bottom: 1px solid #ccc;
	display: block;
	width: 100%;
	padding: 15px;
	font-size: 14px;
	margin-bottom: 10px;
	outline: none;
}
.auth--container input:focus{
	border-bottom: 1px solid #000;
}
.auth--container button{
	display: inline-block;
	padding: 15px 35px;
	color: #fff;
	background: #4D8FAC;
	transition: background .25s ease;
	cursor: pointer;
	border: none;
}
.auth--container button:hover{
	background: #5D8CAE;
}
